.hero-section {
  max-width: 1440px;
  min-height: 100px;
  display: grid;
  grid-template-columns: 1fr 1fr;
  justify-items: center;
  -webkit-box-align: stretch;
  -ms-flex-align: stretch;
  align-items: stretch;
  -ms-flex-line-pack: stretch;
  align-content: stretch;
  -webkit-box-pack: stretch;
  -ms-flex-pack: stretch;
  justify-content: stretch;
  padding: 5vh 60px 10vh 60px;
  margin: auto;
}

.left-block {
  -ms-flex-item-align: center;
  align-self: center;
  justify-self: center;
  display: block;
  padding-left: 60px;
  margin-right: 30px;
  h1 {
    color: #140303;
    font-weight: 600;
    font-size: 4em;
    letter-spacing: -0.01em;
    line-height: 1.12em;
  }
  p {
    color: black;
    font-weight: 500;
    line-height: 1.125em;
    font-size: 1em;
    letter-spacing: -0.0062em;
    margin: 1.85em 0;
  }
  &__container-cta {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: horizontal;
    -webkit-box-direction: normal;
    -ms-flex-flow: row wrap;
    flex-flow: row wrap;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    -ms-flex-line-pack: stretch;
    align-content: stretch;
    margin-bottom: 1.7em;
  }
  &__cta-text {
    color: #140303;
    font-weight: 900;
    font-size: 2em;
    margin: 0 0.7em 0.5em 0;
  }
  &__btn-phone-no {
    display: -webkit-inline-box;
    display: -ms-inline-flexbox;
    display: inline-flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
    -ms-flex-direction: column;
    flex-direction: column;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    justify-content: center;
    text-align: center;
    margin-left: 20px;
    padding: 5px 15px;
    position: relative;
    border: none;
    background-color: rgba(47, 72, 156, 0.4);
    color: #140303;
    font-weight: 600;
    line-height: 1.3em;
    font-size: 1.9em;
    border-radius: 5px;
    -webkit-box-shadow: 3px 4px 5px -3px #000;
    box-shadow: 3px 4px 5px -3px #000;
    &:hover {
      -webkit-transition: all 0.2s ease-out;
      -o-transition: all 0.2s ease-out;
      transition: all 0.2s ease-out;
      border-radius: 5px;
      background-color: rgba(42, 94, 145, 0.15);
      -webkit-box-shadow: 5px 6px 15px -6px #000;
      box-shadow: 5px 6px 15px -6px #000;
      cursor: pointer;
    }
  }

  &__btn-borders {
    min-width: 8em;
    min-height: 2.1em;
    position: absolute;
    bottom: 10%;
    left: -9%;
    z-index: 1;
    border: 3px solid #9c832f;
    border-radius: 5px;
    background: transparent;
    -webkit-box-shadow: 3px 4px 5px -3px #000;
    box-shadow: 3px 4px 5px -3px #000;
    &:hover {
      border-radius: 5px;
      -webkit-box-shadow: 5px 6px 15px -6px #000;
      box-shadow: 5px 6px 15px -6px #000;
      cursor: pointer;
      border-color: rgba(156, 131, 47, 0.61);
    }

    &--purple {
      min-height: 2.2em;
      bottom: -12%;
      left: -2%;
      z-index: 2;
      border-color: #4c2f9c;
      &:hover {
        -webkit-transition: all 0.2s ease-out;
        -o-transition: all 0.2s ease-out;
        transition: all 0.2s ease-out;
        border-radius: 5px;
        -webkit-box-shadow: 5px 6px 15px -6px #000;
        box-shadow: 5px 6px 15px -6px #000;
        cursor: pointer;
        border-color: rgba(76, 47, 156, 0.61);
      }
    }
  }
}

.right-block {
  justify-self: end;
  padding-right: 60px;
  img {
    max-width: 100%;
  }
}
